<template>
    <div>
        {{ this.$store.state.action }} sur : <br>
        <form action="">
            <div v-if="this.$store.state.action == 'Transfer'">
                <label for="destina">Nom du destinataire</label>
                <input type="text" placeholder="Nom du destinataire"/> <br>

            </div>
            <label for="numero">numero de telephone</label>
            <input type="text" 
                placeholder="Numero du destinataire"
                v-model="number_to_deb"/><br>
            <label for="montat">Montant</label>
            <input type="number" 
                placeholder="montant"
                v-model="amount_to_deb"><br>
            <label for="password">Mot de Passe</label>
            <input type="password" 
                placeholder="Votre mot de passe"
                v-model="password">
            <input type="button" value="SEND" @click="send">
        </form>
    </div>
</template>

<script>
export default {

    data(){
        return {
            number_to_deb : '',
            amount_to_deb : '',
            password : '',

        }
    },
    methods: {
        async send(){
            console.log("You want to send : ", this.amount_to_deb)
            try{
                const reque = await fetch('http://127.0.0.1:8000/jov/api/reque/notOwner/', {
                    method : 'POST',
                    headers : {
                        'Content-Type' : 'application/json',
                        'Authorization': 'Bearer ' + this.$store.state.user.access
                    },
                    body : JSON.stringify({
                        number_to_deb : this.number_to_deb,
                        amount_to_deb : this.amount_to_deb,
                        password : this.password,
                    })
                });
                const respo = await reque.json()
                console.log("THe Answer is : ", respo)
            } catch(error){
                console.log("The erro is : ", error)
            } finally{
                console.log("TermiNATED to SEND")
            }
        },
    }
    
}
</script>